/**
 * 
 */

import http from '@/utils/request.js'
/**
 * 
 * url: /mp/v1_0/user/images
 * collect , true, false
 * page    
 * per_page , , 20
 */
// :
// 1. get/delete, params, , 
// 2. put/patch/post, data, 
export const reqGetImages = (paramsObj) => {
  return http({
    method: 'get',
    url: '/mp/v1_0/user/images',
    params: paramsObj || {}
  })
}

/**
 * / ()
 * id:  id
 * collect: , , true, false
 */
export const reqCollectImage = (id, collect) => {
  return http({
    method: 'put',
    url: `/mp/v1_0/user/images/${id}`,
    data: {
      collect
    }
  })
}

/**
 * 
 * @param {*} id id
 */
export const reqDeleteImage = (id) => {
  return http({
    method: 'delete',
    url: `/mp/v1_0/user/images/${id}`
  })
}
