<template>
  <div class="my-cover">
    <!--  -->
    <div class="btn_img" @click="openDialog">
      <!-- , ,  -->
      <!-- ,  -->
      <img :src="value || imgSrc" />
    </div>
    <!--  -->
    <el-dialog
      title=""
      :append-to-body="true"
      :visible.sync="dialogVisible"
      width="720px">

      <!-- tab -->
      <!-- v-model="activeName" tab, name -->
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="image">
          <div class="top-box">
            <el-radio-group @change="changeCollect" size="medium" v-model="collect">
              <el-radio-button :label="false"></el-radio-button>
              <el-radio-button :label="true"></el-radio-button>
            </el-radio-group>
          </div>

          <el-row :gutter="10" class="img_list">
            <el-col
              @click.native="changeSelect(item)"
              v-for="item in images"
              :key="item.id"
              class="img_item"
              :class="{ selected: selectedImageUrl === item.url }"
              :xs="12" :sm="8" :md="6" :lg="4" :xl="4">
              <el-image style="height: 100px; width: 100%;" :src="item.url" fit="cover"></el-image>
            </el-col>
          </el-row>

          <el-pagination
            layout="prev, pager, next"
            @current-change="handleCurrentChange"
            background
            :page-size="per_page"
            :current-page="currentPage"
            :total="total_count">
          </el-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="upload">
          <el-upload
            class="avatar-uploader"
            :action="$http.defaults.baseURL  + '/mp/v1_0/user/images'"
            :show-file-list="false"
            :headers="headers"
            name="image"
            :on-success="handleSuccess">
            <el-image v-if="imageUrl" :src="imageUrl" class="avatar"></el-image>
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-tab-pane>
      </el-tabs>

      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="handleConfirm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { reqGetImages } from '@/api/pictures.js'
import { getUser } from '@/utils/storage.js'
import DefaultImg from '@/assets/default.png'

export default {
  name: 'MyCover',
  props: ['value'], // 
  data () {
    return {
      // 
      dialogVisible: false,
      // tabsname
      activeName: 'image',
      images: [], // 
      collect: false, // 
      per_page: 2, // 
      currentPage: 1, // 
      total_count: 0, // 
      selectedImageUrl: '', //  ()
      imageUrl: '', //  ()
      imgSrc: DefaultImg,
      headers: {
        Authorization: `Bearer ${getUser().token}`
      }
    }
  },
  created () {

  },
  methods: {
    openDialog () {
      // 
      this.dialogVisible = true
      // , 
      this.loadImages()
    },
    async loadImages () {
      // 
      const res = await reqGetImages({
        collects: this.collect,
        page: this.currentPage,
        per_page: this.per_page
      })
      this.images = res.data.data.results
      this.total_count = res.data.data.total_count
      console.log(this.images, this.total_count)
    },
    handleCurrentChange (val) {
      // val 
      // 
      this.currentPage = val
      this.loadImages()
    },
    changeCollect () {
      this.currentPage = 1
      this.loadImages()
    },
    changeSelect (item) {
      this.selectedImageUrl = item.url
    },
    handleSuccess (res, file) {
      this.imageUrl = res.data.url
      this.$message.success('')
    },
    // , 
    handleConfirm () {
      // , , 
      if (this.activeName === 'image') {
        // 
        if (!this.selectedImageUrl) {
          this.$message.error('')
          return
        }
        this.imgSrc = this.selectedImageUrl
      } else if (this.activeName === 'upload') {
        // 
        if (!this.imageUrl) {
          this.$message.error('')
          return
        }
        this.imgSrc = this.imageUrl
      }
      // , 
      this.$emit('input', this.imgSrc)
      // 
      this.dialogVisible = false
    }
  }
}
</script>

<style scoped lang='less'>
// 
.btn_img {
  width: 150px;
  height: 150px;
  border: 1px dashed #ddd;
  img {
    width: 100%;
    height: 100%;
    display: block;
  }
}

// 
.img_list {
  margin-top: 15px;
  .img_item{
    width: 150px;
    height: 120px;
    border: 1px dashed #ddd;
    display: inline-block;
    margin-right: 15px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}

// <div class="selected"></div>
.selected::after{
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.3) url(../assets/selected.png) no-repeat center / 50px 50px;
}

.avatar-uploader {
    text-align: center;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}
.avatar-uploader /deep/ .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
</style>
